//////////////////////
// colors
//////////////////////

@color-primary: #00B0FF;
@color-primary-75: fade(@color-primary, 75%);
@color-primary-25: lighten(@color-primary, 25%);

@color-text: #000000;
@color-text-75: lighten(@color-text, 75%);

@color-background: #ffffff;

@color-red: #d14;

@color-white: #ffffff;
@color-white-75: fade(@color-white, 75%);
@color-white-50: fade(@color-white, 50%);

@color-grey: #f8f8f8;

@color-black: #000000;
@color-black-75: fade(@color-white, 75%);

//////////////////////
// media queries
//////////////////////

// @media @min-sm {}
// @media @max-md {}
// @media @min-sm and @max-md {}

@query-min-xs: 480px;
@query-min-sm: 768px;
@query-min-md: 992px;
@query-min-lg: 1200px;
@query-min-xlg: 1450px;
@min-xs: ~"(min-width: @{query-min-xs})";
@min-sm: ~"(min-width: @{query-min-sm})";
@min-md: ~"(min-width: @{query-min-md})";
@min-lg: ~"(min-width: @{query-min-lg})";
@min-xlg: ~"(min-width: @{query-min-xlg})";

@query-max-xs: 479px;
@query-max-sm: 767px;
@query-max-md: 991px;
@query-max-lg: 1199px;
@query-max-xlg: 1449px;
@max-xs: ~"(max-width: @{query-max-xs})";
@max-sm: ~"(max-width: @{query-max-sm})";
@max-md: ~"(max-width: @{query-max-md})";
@max-lg: ~"(max-width: @{query-max-lg})";
@max-xlg: ~"(max-width: @{query-max-xlg})";

//////////////////////
// fonts
//////////////////////

// .font-sans-light();
.font-sans-light() {
  font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 300;
  font-style: normal;
}

// .font-sans-regular();
.font-sans-regular() {
  font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 400;
  font-style: normal;
}

// .font-sans-medium();
.font-sans-medium() {
  font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 600;
  font-style: normal;
}

// .font-monospace();
.font-monospace() {
  font-family: monaco, Consolas, "Lucida Console", monospace;
  font-weight: normal;
  font-style: normal;
}

//////////////////////
// spacing
//////////////////////

// .spacing-headers();
.spacing-headers() {
  margin-top: 2.619rem;
  margin-bottom: 1.238rem;
  &:first-child {
    margin-top: 0 !important;
  }
}

// .spacing-headers-small();
.spacing-headers-small() {
  margin-top: 1.238rem;
  margin-bottom: 1rem;
  &:first-child {
    margin-top: 0 !important;
  }
}

// .spacing-text();
.spacing-text() {
  margin-bottom: 1rem;
  &:last-child {
    margin-bottom: 0 !important;
  }
}
